import {Injectable} from '@angular/core';
import {Socket} from 'ngx-socket-io';


@Injectable()
export class AfricleanWebsocketProvider {


    constructor(private socket: Socket) {
        // this.socket.connect();
    }

    /**
     * Emet un message
     * @param event vnement mis
     * @param eventValue valeur de l'venement
     * @param callback fonction callback
     */
    // tslint:disable-next-line: ban-types
    public emit(event: string, eventValue?: any, callback?: Function) {
        return this.socket.emit(event, eventValue, callback);
    }

    /**
     * Roit un message
     * @param event vnement mis
     * @param callback fonction  xecuter
     */
    public receive(event: string) {
        this.socket.fromEvent(event).subscribe(
            (result) => {
            }
        )
        return this.socket.fromEvent(event);
    }

    /**
     * Dconnecte la communication
     */
    public disconnect() {
        this.socket.disconnect();
    }
}
