import { AfricleanHttpProvider } from './africlean-http.provider';
import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';

@Injectable()
export class AfricleanHttpLocalisationProvider extends AfricleanHttpProvider {


    /**
     * Rcupre tous les pays
     * @param url url
     * @param options options
     */
    public getCountries(url: string, options?: {}): Observable<any[]> {
        return this.get(url, options);
    }

    /**
     * Rcupre toutes les regions d'un pays
     * @param url url
     * @param options options
     */
    public getRegionsByCountryId(url: string, options?: {}): Observable<any[]> {
        return this.get(url, options);
    }

    /**
     * Rcupre toutes les communes d'une region
     * @param url url
     * @param options options
     */
    public getCommunesByRegionId(url: string, options?: {}): Observable<any[]> {
        return this.get(url, options);
    }

    /**
     * Rcupre tous les quartiers d'une commune
     * @param url url
     * @param options options
     */
    public getDistrictsByCommuneId(url: string, options?: {}): Observable<any[]> {
        return this.get(url, options);
    }

    /**
     * Rcupre tous les secteurs d'un quartiers
     * @param url url
     * @param options options
     */
    public getSectorsByDistrictId(url: string, options?: {}): Observable<any[]> {
        return this.get(url, options);
    }

    /**
     * Rcupre toutes les maisons d'un secteur
     * @param url url
     * @param options options
     */
    public getHomesBySectorId(url: string, options?: {}): Observable<any[]> {
        return this.get(url, options);
    }


    /**
     * Rcupre une maison  partir de son id
     * @param url url
     * @param options options
     */
    public getHomeById(url: string, options?: {}): Observable<any> {
        return this.get(url, options);
    }

    /**
     * Rcupre un secteur  partir de son id
     * @param url url
     * @param options options
     */
    public getSectorById(url: string, options?: {}): Observable<any> {
        return this.get(url, options);
    }
    /**
     * Rcupre un quartier  partir de son id
     * @param url url
     * @param options options
     */
    public getDistrictById(url: string, options?: {}): Observable<any> {
        return this.get(url, options);
    }
    /**
     * Rcupre une commune  partir de son id
     * @param url url
     * @param options options
     */
    public getTownById(url: string, options?: {}): Observable<any> {
        return this.get(url, options);
    }

    /**
     * Rcupre une region  partir de son id
     * @param url url
     * @param options options
     */
    public getRegionById(url: string, options?: {}): Observable<any> {
        return this.get(url, options);
    }

    /**
     * Rcupre un pays  partir de son id
     * @param url url
     * @param options options
     */
    public getCountryById(url: string, options?: {}): Observable<any> {
        return this.get(url, options);
    }
    /**
     * Enrgistre d'une localisation
     * @param url url
     * @param body corp de la requte
     * @param options options
     */
    public save(url: string, body: {}, options?: {}): Observable<any> {
        return this.post(url, body, options);
    }

    /**
     * Suppression d'une localisation
     * @param url url
     * @param options options
     */
    public delateLocation(url: string, options?: {}): Observable<any> {
        return this.delete(url, options);
    }
}
